}

.flex__center {
    display: flex;
    justify-content: center;
    align-items: center;
}
@-webkit-keyframes fadeInFromBottom {
    0% {
        opacity: 0;
        -webkit-transform: translateY(10%);
                transform: translateY(10%);
    }
    100% {
        opacity: 1;
        -webkit-transform: translateY(0);
                transform: translateY(0);
    }
}

@keyframes fadeInFromBottom {
    0% {
        opacity: 0;
        -webkit-transform: translateY(10%);
                transform: translateY(10%);
    }
    100% {
        opacity: 1;
        -webkit-transform: translateY(0);
                transform: translateY(0);
    }
}

