.marquee {
    white-space: nowrap;
    overflow: hidden;
    background: var(--color-black);
    color: var(--color-white);
    width: 100vw;
    height: 34px;
    font-size: 28px;
}

.marquee span {
    display: inline-block;
    -webkit-animation: marqueeAnimation 10s linear infinite;
            animation: marqueeAnimation 10s linear infinite;
}

