.animation-from-top {
    -webkit-animation: animationFromTop 1s ease-in-out forwards;
            animation: animationFromTop 1s ease-in-out forwards;

}

@-webkit-keyframes animationFromTop {
    0% {
        -webkit-transform: translateY(-10%);
                transform: translateY(-10%);
        opacity: 0;
        transition: opacity 0.5s ease, -webkit-transform 0.5s ease;
        transition: transform 0.5s ease, opacity 0.5s ease;
        transition: transform 0.5s ease, opacity 0.5s ease, -webkit-transform 0.5s ease;
    }
    100% {
        -webkit-transform: translateY(0);
                transform: translateY(0);
        opacity: 1;
    }
}

@keyframes animationFromTop {
    0% {
        -webkit-transform: translateY(-10%);
                transform: translateY(-10%);
        opacity: 0;
        transition: opacity 0.5s ease, -webkit-transform 0.5s ease;
        transition: transform 0.5s ease, opacity 0.5s ease;
        transition: transform 0.5s ease, opacity 0.5s ease, -webkit-transform 0.5s ease;
    }
    100% {
        -webkit-transform: translateY(0);
                transform: translateY(0);
        opacity: 1;
    }
}

.animation-rotate {
    -webkit-animation: animationRotate 1s ease-in-out forwards;
            animation: animationRotate 1s ease-in-out forwards;
}

@-webkit-keyframes animationRotate {
    0% {
        -webkit-transform: rotateY(180deg);
                transform: rotateY(180deg);
        opacity: 0;
        transition: opacity 0.5s ease, -webkit-transform 0.5s ease;
        transition: transform 0.5s ease, opacity 0.5s ease;
        transition: transform 0.5s ease, opacity 0.5s ease, -webkit-transform 0.5s ease;
    }
    100% {
        -webkit-transform: rotateY(0deg);
                transform: rotateY(0deg);
        opacity: 1;
    }
}

@keyframes animationRotate {
    0% {
        -webkit-transform: rotateY(180deg);
                transform: rotateY(180deg);
        opacity: 0;
        transition: opacity 0.5s ease, -webkit-transform 0.5s ease;
        transition: transform 0.5s ease, opacity 0.5s ease;
        transition: transform 0.5s ease, opacity 0.5s ease, -webkit-transform 0.5s ease;
    }
    100% {
        -webkit-transform: rotateY(0deg);
                transform: rotateY(0deg);
        opacity: 1;
    }
}

